<template>
    <div class="container">
        <h2 class="page-header"><i18n path="common.install"></i18n></h2>
        <hr class="my-2">
        <p><i18n path="common.guide.useNpm"></i18n></p>
        <pre class="brush:bash">
            npm i v-uploader --save
        </pre>
        <p><i18n path="common.guide.include"></i18n></p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vUploader from 'v-uploader';

            // v-uploader plugin global config
            const uploaderConfig = {
                // file uploader service url
                uploadFileUrl: 'http://xxx/upload/publicFileUpload',
                // file delete service url
                deleteFileUrl: 'http://xxx/upload/deleteUploadFile',
                // set the way to show upload message(upload fail message)
                showMessage: (vue, message) => {
                    //using v-dialogs to show message
                    vue.$vDialog.alert(message, null, {messageType: 'error'});
                }
            };

            // install plugin with options
            Vue.use(vUploader, uploaderConfig);
        </pre>

        <div class="card p-4 text-monospace">
            <p><i18n path="common.globalConfig"></i18n></p>
            <ul>
                <li>language</li>
                <li>preview</li>
                <li>buttonText</li>
                <li>buttonIcon</li>
                <li>previewWidth</li>
                <li>previewHeight</li>
                <li>multiple</li>
                <li>uploadFileObjName</li>
                <li>itemLimit</li>
                <li>fileSizeLimit</li>
                <li>fileTypeExts</li>
                <li>imageMaxHeight</li>
                <li>imageMaxWidth</li>
                <li>imageMinHeight</li>
                <li>imageMinWidth</li>
                <li>uploadFileUrl (<i18n path="common.tbTitle.required"></i18n>)</li>
                <li>deleteFileUrl (<i18n path="common.tbTitle.required"></i18n>)</li>
                <li>showMessage (<i18n path="common.tbTitle.required"></i18n>)</li>
            </ul>
        </div>

        <p><i18n path="upload.useDialog"></i18n></p>
        <br><br>

        <h2 class="page-header"><i18n path="common.deploy"></i18n></h2>
        <hr class="my-2">
        <p>template</p>
        <script type="syntaxhighlighter" class="brush:js">
        <![CDATA[
            <template>
              <!-- bind event 'done' to receive file uploaded info -->
              <v-uploader @done="uploadDone" ></v-uploader>
            </template>
        ]]>
        </script>

        <p>script</p>
        <pre class="brush:js">
        export default {
            methods:{
                //receive uploaded files info
                //@type Array
                uploadDone(files){
                    if(files && Array.isArray(files) && files.length){
                        // do something...
                    }
                }
            }
        };
        </pre>
        <br><br>

        <h2 class="page-header"><i18n path="common.example"></i18n> <small><i18n path="upload.demo.title"></i18n></small></h2>
        <hr class="my-2">

        <h3><i18n path="upload.demo.single"></i18n></h3>
        <v-uploader @done="uploadDone" ></v-uploader>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-uploader @done="uploadDone" ></v-uploader>

            < script >
            export default {
                methods: {
                    uploadDone(files){
                        if(files && Array.isArray(files) && files.length){
                            console.log(files);
                        }
                    }
                }
            };
            < /script >
            ]]>
            </script>
        </showcode>

        <p><br></p>

        <h3><i18n path="upload.demo.customSize"></i18n></h3>
        <v-uploader :preview-width="400" :preview-height="300" ></v-uploader>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-uploader :preview-width="400" :preview-height="300" ></v-uploader>
            ]]>
            </script>
        </showcode>

        <p><br></p>

        <h3><i18n path="upload.demo.noPreview"></i18n></h3>
        <v-uploader :preview="false" @done="noPreUploaded" ></v-uploader>
        <h4><i18n path="upload.demo.uploadedInfo"></i18n></h4>
        <pre style="border: 0;">{{JSON.stringify(uploaded, null, 4)}}</pre>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-uploader :preview="false" ></v-uploader>
            ]]>
            </script>
        </showcode>
        <br>

        <h3><i18n path="upload.demo.buttonText"></i18n></h3>
        <v-uploader button-text="Select file to upload" ></v-uploader>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-uploader button-text="Select file to upload" ></v-uploader>
            ]]>
            </script>
        </showcode>

        <h3><i18n path="upload.demo.noIcon"></i18n></h3>
        <v-uploader :button-icon="false" ></v-uploader>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-uploader :button-icon="false" ></v-uploader>
            ]]>
            </script>
        </showcode>

        <h3><i18n path="upload.demo.customPreviewImage"></i18n></h3>
        <v-uploader :preview-img="require('../../assets/logo.png')" ></v-uploader>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-uploader preview-img="http://xxx.com/a.jpg" ></v-uploader>
            ]]>
            </script>
        </showcode>


        <h3><i18n path="upload.demo.multiple"></i18n> <small><i18n path="upload.demo.multipleSub"></i18n></small></h3>
        <v-uploader :multiple="true" @done="uploadDone" ></v-uploader>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-uploader :multiple="true" ></v-uploader>
            ]]>
            </script>
        </showcode>
        <br><br>


