                        </ul>
                    </td>
                    <td>false</td>
                    <td>'cn'</td>
                </tr>
                <tr>
                    <td>preview</td>
                    <td>boolean</td>
                    <td><i18n path="upload.option.preview"></i18n></td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>preview-img</td>
                    <td>string</td>
                    <td><i18n path="upload.option.previewImg"></i18n></td>
