                </tr>
                <tr>
                    <td>preview-width</td>
                    <td>number</td>
                    <td><i18n path="upload.option.previewWidth"></i18n></td>
                    <td>false</td>
                    <td>200</td>
                </tr>
                <tr>
                    <td>preview-height</td>
                    <td>number</td>
                    <td><i18n path="upload.option.previewHeight"></i18n></td>
                    <td>false</td>
                    <td>150</td>
                </tr>
                <tr>
                    <td>file-size-limit</td>
                    <td>string</td>
                    <td><i18n path="upload.option.fileSizeLimit"></i18n></td>
                    <td>false</td>
                    <td>'5MB'</td>
                </tr>
                <tr>
                    <td>file-type-exts</td>
