                <tr>
                    <td>image-min-width</td>
                    <td>number</td>
                    <td><i18n path="upload.option.imageMinWidth"></i18n></td>
                    <td>false</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>button-text</td>
                    <td>string</td>
                    <td><i18n path="upload.option.buttonText"></i18n></td>
                    <td>false</td>
