                </tr>
                <tr>
                    <td>item-limit</td>
                    <td>number</td>
                    <td><i18n path="upload.option.itemLimit"></i18n></td>
                    <td>false</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>before-upload</td>
                    <td>function</td>
                    <td><i18n path="upload.option.beforeUpload"></i18n></td>
                    <td>false</td>
                    <td></td>
                </tr>
            </tbody>
        </table>
        <br><br>

        <h2 class="page-header"><i18n path="common.events"></i18n></h2>
        <table class="table table-bordered table-striped text-monospace">
            <thead>
            <tr>
                <th><i18n path="common.tbTitle.param"></i18n></th>
                <th><i18n path="common.tbTitle.desc"></i18n></th>
